@import url('https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,500,500i|Open+Sans:300,300i,400,400i,600,600i|Playfair+Display:400i');

$font-main: 'Montserrat', sans-serif;
$font-secondary: 'Open Sans', sans-serif;
$font-tertiary: 'Playfair Display', serif;

$body-font-size: 18px;
$nav-font-size: 16px;

$theme-main-color: rgba(29, 42, 75, 1);
$theme-secondary-color: rgba(20, 132, 153, 1);
$theme-accent-color: rgba(249, 209, 29, 1);

$white: rgba(255, 255, 255, 1);
$dark-grey: #333;

$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;


html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    margin: 0;
    padding: 0;

    vertical-align: baseline;

    border: 0;
    outline: 0;

    font-family: inherit;
    font-size: 100%;
    font-weight: inherit;
    font-style: inherit;
}

:focus {
 /* remember to define focus styles! */
    outline: 0;
}

html {
    min-width: 10px;
    height: 100%;

    -webkit-text-size-adjust: 100%;
    -webkit-appearance: none;
}

p {
    margin: 1rem 0;
}

sup {
    vertical-align: super;

    font-size: 14px;
}

ol, ul {
    list-style: none;
}

table {
    width: 100%;

    border-spacing: 0;
 /* tables still need 'cellspacing="0"' in the markup */

    border-collapse: separate;
}

caption, th, td {
    text-align: left;

    font-weight: normal;
}

blockquote:before, blockquote:after, q:before, q:after {
    content: "";
}

blockquote, q {
    quotes: "" "";
}

blockquote {
    margin: 3rem;
    padding: 0 3rem;

    text-align: left;

    border-left: 3px solid #ebebeb;

    font-family: 'Open Sans', serif;
    font-size: 28px;
    font-weight: normal;

    span {
        font-weight: normal;
        font-style: italic;
    }
}

blockquote p {
    margin: 10px 0 !important;
}

em, i {
    font-style: italic;
}

hr {
    border-width: 0;
}

hr {
    clear: both;

    height: 1px;
    margin: 3em 0;

    border: 0;
    background: #bdbdbd;
}

a img {
    border: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

a {
    text-decoration: underline;
}

a:hover {
    text-decoration: none;
}

a img {
    border: 0;
}

.alignleft {
    float: left;

    margin: 10px 30px 20px 0;
}

.alignright {
    float: right;

    margin: 10px 0 20px 30px;
}

.aligncenter {
    display: block;
    float: none;

    margin: 15px auto !important;
}

.flex-center {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -ms-flex-align: center;
}

.justify-center {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

/* Mobile Nav Icon Buttons */
.mobile-nav {
    position: absolute;
    z-index: 999;
    top: 0.75rem;
}

.mobile-nav li {
    display: inline-block;
}

.mobile-nav li a {
    margin: 0 0.125rem;
    padding: 10px 0.5rem;

    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;

    color: $white;
}

.mobile-nav li a i {
    font-size: 24px;
}

.mobile-nav li a:hover {
    color: $theme-accent-color;
}

/*** Structures and Defaults
****************************************************************/
body {
    text-align: left;
    text-transform: none;

    color: #65696e;
    background-color: #fff;
    background-attachment: scroll;

    font-family: $font-secondary;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;

    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -webkit-text-size-adjust: 100%;
}

#main {
    position: relative;

    display: block;

    width: 100%;
    min-height: 700px;
    margin: 0;
    padding-bottom: 0;

    background-color: #fff;
}

/*** Fonts and Social Media ICOs
****************************************************************/
@font-face {
    font-family: 'icomoon'; font-weight: normal; font-style: normal;src: url("fonts/icomoon.eot?i38w3y"); src: url("fonts/icomoon.eot?i38w3y#iefix") format("embedded-opentype"), url("fonts/icomoon.ttf?i38w3y") format("truetype"), url("fonts/icomoon.woff?i38w3y") format("woff"), url("fonts/icomoon.svg?i38w3y#icomoon") format("svg");
}

[class^="icon-"], [class*=" icon-"] {
    text-transform: none;

    font-family: 'icomoon';
    font-weight: normal;
    font-style: normal;
    font-variant: normal;
    line-height: 1;

    speak: none;
 /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-google-plus:before {
    content: "\78";
}

.icon-blogger:before {
    content: "\62";
}

.icon-facebook:before {
    content: "\66";
}

.icon-linkedin:before {
    content: "\69";
}

.icon-yelp:before {
    content: "\70";
}

.icon-instagram:before {
    content: "\72";
}

.icon-twitter:before {
    content: "\74";
}

.icon-foursquare:before {
    content: "\75";
}

.icon-vimeo:before {
    content: "\76";
}

.icon-youtube:before {
    content: "\79";
}

.icon-pinterest:before {
    content: "\64";
}

.icon-tumblr:before {
    content: "\77";
}

/*** Typography
****************************************************************/
h1, h2, h3, h4, h5, h6, h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    margin: 0.5em 0;

    text-transform: none;
    word-wrap: break-word;

    font-family: $font-main;
    font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
    text-transform: uppercase;

    color: #5F6369;
}

#content h1, #content h2, #content h3 {
    border-bottom: none;
}

h1 {
    font-size: 2.625em;
}

h1 {
    padding: 0 0 5px;

    font-size: 64px;
}

h1 a {
    display: block;

    text-decoration: none;
}

h2 {
    padding: 0 0 5px;

    font-size: 38px;
}

h3 {
    padding: 0 0 5px;

    font-size: 32px;
}

h4 {
    font-size: 30px;
}

h5 {
    font-size: 24px;
}

h6 {
    font-size: 20px;
}

.wp-caption-text {
    text-align: center;
}

.wp-caption-text span {
    text-transform: uppercase;

    font-family: 'Open Sans', Arial, Helvetica, sans-serif;
    font-size: 18px;
}

/*** Links
****************************************************************/
a {
    text-decoration: none;

    color: $theme-main-color;

    font-weight: normal;
    &:hover,
    &:focus,
    &:active {
        text-decoration: none;

        color: $theme-secondary-color;
        outline: 0;
    }
}

::-moz-selection {
    color: #fff;
    background: $theme-accent-color;
    text-shadow: none;
}

::selection {
    color: #fff;
    background: $theme-accent-color;
    text-shadow: none;
}

a.button:after {
    content: '';
}

.btn-primary, a.btn-primary, a.btn-secondary, .widget .wpcf7 input[type="submit"], #primary .wpcf7 input[type="submit"], .float-box .wpcf7 input[type="submit"] {
    margin: 2rem 0;
    padding-top: 1rem;
    padding-bottom: 1rem;

    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
    white-space: normal;
    letter-spacing: 2px;
    text-transform: uppercase;

    color: $white;
    border: 1px solid $theme-accent-color;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
    background: $theme-accent-color;

    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: 400;
}

.btn-primary:hover, a.btn-primary:hover, a.btn-primary:focus, .widget .wpcf7 input[type="submit"]:hover, #primary .wpcf7 input[type="submit"]:hover, .float-box .wpcf7 input[type="submit"]:hover {
    cursor: pointer;

    color: $white;
    border: 1px solid $theme-main-color;
    outline: 0;
    background: $theme-main-color !important;
}

.btn-primary.focus, .btn-primary:focus {
    -webkit-box-shadow: 0 0 0 2px rgba($theme-secondary-color,.5);
    box-shadow: 0 0 0 2px rgba($theme-secondary-color, 0.5);
}

.float-box .wpcf7 input[type="submit"] {
    color: #fff;
    background: #262626;
}

.btn-secondary, a.btn-secondary, #respond input#submit {
    text-transform: uppercase;

    color: #0C739E;
    border: 2px solid #0C739E;
    background: transparent;

    font-family: 'Open Sans', sans-serif;
}

.btn-secondary:hover, a.btn-secondary:hover, a.btn-secondary:focus, #respond input#submit:hover {
    color: #0C739E;
    border: 2px solid #e0e0e0;
    background: #fff;
}

.slider .btn, .slider a.btn {
    color: #fff;
    border: 2px solid #B6C988;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    background: #B6C988;

    font-weight: 400;
}

.slider .btn:hover, .slider a.btn:hover, .slider a.btn:focus, .slider a.btn:active:focus, .slider a.btn:active {
    color: #B6C988;
    border: 2px solid #B6C988;
    background: transparent !important;
}

.widget .wpcf7 input[type="submit"] {
    width: 100% !important;
    height: 75px;
    margin-top: 0.5em !important;
}

.float-box {
    position: fixed;
    z-index: 99999;
    top: 25%;
    right: -60%;

    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 60%;
    padding: 20px;

    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;

    background: #B6C988;
}

.float-box.hidden-reveal {
    position: fixed;
    z-index: 99999;
    top: 25%;
    right: 0;

    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 60%;
    padding: 20px;
}

.float-box p {
    margin: 0 0 1em 0;

    text-align: center;

    color: #fff;
}

.float-box address {
    color: #fff;

    font-weight: 400;
}

.float-box a.clicker {
    position: absolute;
    top: 0;
    left: -58px;

    display: block;

    height: 100%;

    color: #fff;
    background: transparent url("img/clicker.png") no-repeat scroll left top;
    background-size: 58px 100%;

    font-family: 'Open Sans', sans-serif;
}

.float-box a.clicker span {
    position: relative;
    top: 47%;
    right: 20px;

    display: block;

    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
 /* Safari */
    -webkit-transform: rotate(90deg);
 /* Firefox */
    -moz-transform: rotate(90deg);
 /* IE */
    -ms-transform: rotate(90deg);
 /* Opera */
    -o-transform: rotate(90deg);
    text-transform: uppercase;

    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-weight: 400;
}

.float-box a.clicker span:before {
    position: absolute;
    top: 12px;
    left: 40px;

    display: block;

    content: '◂ ';
 /* Safari */
    -webkit-transform: rotate(-90deg);
 /* Firefox */
    -moz-transform: rotate(-90deg);
 /* IE */
    -ms-transform: rotate(-90deg);
 /* Opera */
    -o-transform: rotate(-90deg);

    font-size: 32px;
}

.float-box img {
    display: block;

    height: auto;
}

.float-box .wpcf7 p {
    position: relative;

    display: block;
    float: left;

    width: 100%;
    margin: 0 0 0.5em 0;
}

.float-box .wpcf7 input, .float-box .wpcf7 textarea {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding: 8px;

    text-transform: lowercase;

    color: #333;
    border: 0;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;

    font-size: 14px;
}

.float-box .wpcf7 input[type="submit"] {
    display: block;

    margin: -3px 0px 0px;
}

.float-box .wpcf7 img.ajax-loader {
    position: absolute;
    z-index: 999;
    top: 6px;
    right: 12px;
}

.float-box .wpcf7 textarea {
    height: 75px;

    resize: none;
}

.float-box address p, .float-box address a {
    text-align: left;
}

.float-box address a, .float-box .links a {
    color: #fff;
}

.float-box .links a {
    display: block;

    padding: 2.45rem 0;

    border-bottom: 1px solid #fff;

    font-size: 18px;
}

.float-box .links a:last-child {
    border: 0;
}

.float-box .links a::before {
    display: inline;

    content: '» ';
}

.float-box a:hover, .float-box .links a:hover {
    text-decoration: underline;
}

.float-box .col {
    border-left: 1px solid #fff;
}

/*** Header ***/
#header {
    position: relative;
    z-index: 9999;

    background: transparent;
}

#social::after {
    position: relative;
    z-index: 999;
    bottom: 0;

    display: block;

    width: 90%;
    height: 1px;
    margin-left: 15px;

    content: '';

    background: $theme-accent-color;
}

#site-logo {
    float: left;

    margin: 0;
    padding: 0 5px;
}

#site-meta {
    float: right;

    margin: 8px 15px 0 0;

    text-align: right;

    line-height: 16px;
}

#logo {
    position: relative;

    background: transparent;
}

#logo img {
    display: block;

    height: auto;
    margin: 0 auto;
    padding: 1rem 0;
}

.inside-page .stuck.scrolled #logo {
    opacity: 1;
}

#logo h1 {
    margin: 0;
    padding: 0;

    text-align: center;

    color: #333;

    font-size: 18px;
    font-weight: bold;
}

#logo h1 a {
    margin: 0;
}

.site-name {
    display: block;

    padding-top: 20px;

    font-size: 2.063em;
 /* = 33px */
    line-height: 1.0em;
}

.site-name a {
    color: #0C739E;

    font-size: 28px;
    font-weight: 700;
}

.site-description {
    display: block;

    color: #0e3087;

    font-size: 22px;
    font-weight: 400;
    font-style: italic;
}

/****************************************************************/
/*** Top Navigation ***/
/****************************************************************/
#access {
    position: relative;
    z-index: 9999;
 /* For browsers that do not support gradients */

    display: block;

    width: 100%;

    background: rgba($theme-main-color, .9);
}

#access, .sf-menu, .sf-menu a {
    height: 50px;
}

.sticky-wrapper {
    position: fixed;
    top: 0;

    width: 100%;

    background: transparent;
}

.stuck {
    position: relative;
    z-index: 999999;

    width: 100%;
    height: 100%;

    background: transparent;
}

.sticky {
  padding-top: 1.5rem;
  &.scrolled {
    padding-top: 0;
    & .container {
        width: 100%;
    }
  }
}

.sf-menu a {
    position: relative;

    display: block;
    zoom: 1;

    height: 50px;
    margin: 0;
    padding: 0 1.25rem;

    text-decoration: none;
    text-transform: uppercase;

    color: #ffffff;
    background: none;

    font-family: $font-main;
    font-size: 16px;
    font-weight: 400;
    line-height: 50px;

    &:hover {
        text-decoration: none;

        color: #fff;
        border-bottom: 0;
        background-color: desaturate(lighten($theme-main-color, 20%), 25%);
    }
}

.hover {
    text-decoration: none;

    color: #fff;
    border-bottom: 0;
    background-color: desaturate(lighten($theme-main-color, 20%), 25%) !important;
}

#access #searchform {
    float: right;

    width: 225px;
    padding: 5px 10px;
}

#access #searchform input[type="text"] {
    width: 160px;
    padding: 7px;

    color: #fff;
    border: 0;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    background: #5a606e;
}

#access #searchform input[type="submit"] {
    width: 40px;
    height: 26px;
    padding: 5px;

    border: 0;
    background: url(img/search-button.png) no-repeat left top;
}

#access #searchform input[value="Search"] {
    overflow: hidden;

    white-space: nowrap;
    text-indent: -9999px;

    color: #232B3D;
}

#access #searchform input[type="submit"]:hover {
    cursor: pointer;

    opacity: 0.8;
}

.sf-menu, .sf-menu * {
    margin: 0;
    padding: 0;

    list-style: none;
}

.sf-menu li {
    position: relative;
}

.sf-menu ul {
    position: absolute;
    z-index: 99;
    top: 50px;
    left: 0;

    display: none;
}

.stuck .sf-menu ul {
    position: absolute;
    z-index: 99;
    top: 50px;
    left: 0;

    display: none;
}

.stuck .sf-menu ul ul {
    position: absolute;
    z-index: 99;
    top: 0 !important;
    top: 55px;
    left: 0;

    display: none;
}

.sf-menu > li {
 /* For Hover */
    display: inline-block;
    float: none;

    margin-left: 0;
    padding-bottom: 3rem;
}

.sf-menu > li:hover {
    cursor: pointer;
}

.sf-menu li:hover > ul, .sf-menu li.sfHover > ul {
    display: block;
}

.sf-menu ul ul {
    top: 0;
    left: 100% !important;

    background: #7D8589;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

.sf-menu ul ul ul {
    background: #555;
}

.sf-menu ul ul ul li a:hover {
    color: #111 !important;
    background: #fff !important;
}

/*** point all arrows down ***/
.sf-arrows.sf-navbar ul .sf-with-ul:after {
    margin-top: -3px;
    margin-right: 0;

    border-color: transparent;
    border-top-color: #dFeEFF;
 /* edit this to suit design (no rgba in IE8) */
    border-top-color: rgba(255, 255, 255, 0.5);
}

.sf-arrows.sf-navbar ul > li > .sf-with-ul:focus:after, .sf-arrows.sf-navbar ul > li:hover > .sf-with-ul:after, .sf-arrows.sf-navbar ul > .sfHover > .sf-with-ul:after {
    border-color: transparent;
    border-top-color: white;
}

.sf-menu {
    width: 100%;
    margin: 0 auto;
    margin-left: 0%;

    text-align: center;
}

.sf-menu ul {
    min-width: 12em;

    background: $white;
    -webkit-box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
    box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
}

.sf-menu ul ul li a {
    color: #fff !important;
}

.sf-menu ul ul li a:hover {
    color: #fff !important;
    background: #333 !important;
}

.sf-menu > li:first-child > a {
    border-left: 0;
}

.sf-menu li {
    height: auto;

    white-space: nowrap;

    background: none;
}

.sf-menu ul li a, .sf-menu ul li:first-child a {
    background: none;
}

.sf-menu li li a:hover {
    color: $white !important;
    border: 0 !important;
    background: $theme-accent-color !important;
}

.sf-menu li:hover, .sf-menu li.sfHover, .sf-menu li:hover a, .sf-menu li.sfHover a {
 /* only transition out, not in */
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}

/*** arrows (for all except IE7) **/
.sf-arrows .sf-with-ul {
    padding-right: 2.5em;
 /* no CSS arrows for IE7 (lack pseudo-elements) */
}

/* styling for both css and generated arrows */
.sf-arrows .sf-with-ul:after {
    position: absolute;
    top: 50%;
    right: 1em;

    width: 0;
    height: 0;
    margin-top: -3px;

    content: '';
 /* order of following 3 rules important for fallbacks to work */

    border: 5px solid transparent;
    border-top-color: #dFeEFF;
 /* edit this to suit design (no rgba in IE8) */
    border-top-color: rgba(255, 255, 255, 0.5);
}

.sf-arrows > li > .sf-with-ul:focus:after, .sf-arrows > li:hover > .sf-with-ul:after, .sf-arrows > .sfHover > .sf-with-ul:after {
    border-top-color: white;
 /* IE8 fallback colour */
}

/* styling for right-facing arrows */
.sf-arrows ul .sf-with-ul:after {
    margin-top: -5px;
    margin-right: -3px;

    border-color: transparent;
    border-left-color: #dFeEFF;
 /* edit this to suit design (no rgba in IE8) */
    border-left-color: rgba(255, 255, 255, 0.5);
}

.sf-arrows ul li > .sf-with-ul:focus:after, .sf-arrows ul li:hover > .sf-with-ul:after, .sf-arrows ul .sfHover > .sf-with-ul:after {
    border-left-color: white;
}

.sf-menu li li a, .stuck .sf-menu li li a {
    height: auto;
    margin: 0;
    padding: 10px 15px;

    text-align: left;
    text-transform: none;

    color: #4f595f;
    border: 0;

    font-family: 'Open Sans', Arial, Helvetica, sans-serif;
    line-height: 1;
}

/*** Content ***/
#one-column, #def-column {
    display: block;

    width: 100%;

    background: #FFF;
}

#one-column #primary {
    position: relative;

    display: block;
    float: none;

    margin: 0 auto;
}

#one-column .container {
    padding-top: 35px;
}

#primary {
    position: relative;
}

#primary strong, strong {
    font-weight: bold;
}

.post-entry img {
    max-width: 100%;
    height: auto;
    padding: 0px;

    border: 0;
}

#primary h1.entry-title {
    position: relative;

    display: block;

    text-align: left;
    text-transform: uppercase;

    background: none;

    font-family: 'Open Sans', Helvetica, sans-serif;
    font-size: 48px;
    font-weight: normal;
    line-height: 1;
}

#primary .content-wrapper {
    width: 100%;
    margin: 0 auto;
    padding: 0;
}

#primary ul, #primary ol {
    margin: 0;
    margin-bottom: 1em;
    padding: 0 0 0px 30px;

    list-style: disc;

    color: #333;

    font-size: 18px;
}

#primary ul ul {
    margin: 0;
    padding: 0 0 0 20px;

    list-style: disc;
}

#primary ul li {
    padding-left: 2px;

    list-style: disc;
}

#primary ol li {
    padding-left: 2px;

    list-style: decimal;
}

#primary li li {
    list-style: disc;
}

#primary img {
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
}

.home #primary a {
    text-decoration: none;
}

#primary a {
    text-decoration: none;
}

.home #primary a.button {
    float: left;
}

#primary a img {
    border: 5px solid #ebebeb;
}

#primary a:hover img {
    opacity: 0.8;
}


/*** Sidebar */
#secondary {
    background-color: transparent;
}

#primary, #secondary {
    margin: 35px 0;
}

#secondary .content-wrapper {
    background: transparent;
}

.home #secondary .content-wrapper {
    border: none;
}

#secondary aside {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding: 5%;

    background: transparent;
}

#secondary aside p {
    color: #fff;

    font-family: Helvetica, Arial, sans-serif;
    font-size: 18px;
}

#secondary .widget h3 {
    margin: 15px;
    padding: 5px;

    text-transform: none;

    font-size: 26px;
    font-weight: 300;
}

h3.widget-title {
    position: relative;

    display: block;

    margin: 0 0 10px 0;

    text-transform: uppercase;
}

#secondary .widget h3 {
    position: relative;

    overflow: hidden;

    margin: 15px 0 15px 15px;
    padding: 0;

    text-align: left;
    text-transform: uppercase;

    font-size: 26px;
    font-weight: normal;
}

/* Secondary Columns Styles */
#secondary .widget ul {
    padding: 10px;
}

#secondary .widget li {
    margin: 0;
    padding: 0 0 0 0;

    list-style-type: none;

    word-wrap: break-word;
}

#secondary .widget li li {
    margin: 0 0 0 10px;
    padding: 1px 0;

    list-style: none;
}

#secondary .widget li {
    margin: 6px 0;

    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    text-align: left;
}

#secondary .widget li a {
    padding-left: 0;

    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    text-transform: none;

    font-family: 'Open Sans', Helvetica, sans-serif;
    font-size: 18px;
    font-weight: normal;
    line-height: 1.5em;
}

#secondary .widget li a::before {
    content: '• ';
}

#secondary .widget li a:hover::before {
    content: '• ';
}

#secondary .widget li li a {
    font-size: 26px;
}

#secondary .widget li li a {
    background: none;
}

#secondary .widget li li a::before {
    font-size: 12px;
}

#secondary ul.menu li a {
    float: left;
    overflow: hidden;

    width: 308px;
    height: 110px;
    margin: 8px 0;

    white-space: nowrap;
    text-indent: -9999px;

    border-left: 9px solid #e1e1e1;
    background-color: #000;
}

#secondary ul.menu li:first-child a {
    margin-top: 15px;
}

#secondary ul.menu li a:hover {
    border-left: 9px solid #cc3;
}

/*** Widgets ***/
#widgets {
    margin-top: 40px;
}

.tagcloud {
    padding: 0 35px;
}

#widgets a {
    display: inline-block;

    margin: 0;
    padding: 0;

    text-decoration: none;
}

#widgets form {
    margin: 0;
}

#widgets ul, #widgets ol {
    padding: 0 0 0 20px;
}

#widgets ul li a {
    display: inline;

    text-decoration: none;
}

#widgets .widget-title img {
    position: relative;
    top: 4px;

    float: right;

    width: 11px;
    height: 11px;
}

#widgets .rss-date {
    padding: 6px 12px;

    line-height: 18px;
}

#widgets .rssSummary {
    padding: 10px;
}

#widgets cite {
    padding: 6px 12px;

    font-style: normal;
    line-height: 18px;
}

#widgets .tagcloud, #widgets .textwidget {
    display: block;

    margin: 0;

    word-wrap: break-word;

    line-height: 1.5em;
}

.textwidget {
    line-height: 20px;
}

.textwidget p.dedicated {
    text-align: center;

    color: #C00;

    font-size: 18px;
    font-weight: bold;
}

.textwidget .dedicated span {
    text-transform: uppercase;

    font-size: 14px;
    font-weight: normal;
}

.textwidget img {
    display: block;

    margin: 0 auto;
}

.textwidget a.button {
    position: absolute;
    bottom: 25px;

    margin: 0 auto;
    margin-left: 60px;
}

.textwidget h5 {
    margin: 0;

    color: #609ab5;

    font-family: 'Verdana', Verdana, Geneva, sans-serif;
    font-size: 16px;
    font-weight: bold;
}

.textwidget h5 span {
    margin: 0;

    color: #222;

    font-family: 'Verdana', Verdana, Geneva, sans-serif;
    font-size: 14px;
    font-weight: normal;
}

#widgets .textwidget a {
    display: inline;
}

#widgets ul .children {
    padding: 0 0 0 10px;
}

#widgets .author {
    padding-top: 4px;

    font-weight: 700;
}

.widget_archive select, #cat {
    display: block;

    margin: 0 15px 0 0;
}

.widget-title a {
    padding: 0 !important;

    border-bottom: none;
}

/*** Footer *******/
#footer {
    position: relative;
    z-index: 0;

    display: block;
    float: left;

    width: 100%;

    text-align: center;
}

.home #footer {
