    margin: 0 0 15px;
}

.post-data {
    margin-bottom: 60px;
}

.post-meta {
    margin: 1em 0;
}

.entry-title {
    margin: 0 0 0.5em 0;
    padding: 0 0 0.5em 0;
}

.entry-content {
    padding: 0 0 35px 0;
}

.entry-content p {
    margin: 0 0 1.5em;
}

.entry-title {
    width: 100%;
    margin: 0;
    padding: 0;

    text-align: center;
    text-transform: uppercase;

    color: #0C739E;

    font-family: $font-main;
    font-size: 60px;
    line-height: 1;
