/*** Blog Stylings
****************************************************************/
#nav-single {
    padding: 0 0 30px;
}

#nav-single .nav-previous {
    float: left;
}

#nav-single .nav-next {
    float: right;
}

.navigation {
    padding: 0 0 25px 0;
}

#comments-title {
    padding: 0 0 2.6em;

    text-transform: uppercase;

    color: #666;

    font-size: 10px;
    font-weight: 500;
    line-height: 2.6em;
}

.nopassword, .nocomments {
    margin: 26px 0;

    text-align: center;

    color: #aaa;

    font-size: 24px;
    font-weight: 100;
}

.commentlist {
    width: 68.9%;
    margin: 0 auto;

    list-style: none;
}

.commentlist li {
    list-style: none !important;
}

.content .commentlist, .page-template-sidebar-page-php .commentlist {
    width: 100%;
 /* reset the width for the one-column and sidebar page layout */
}

.commentlist > li.comment {
    position: relative;

    margin: 0 0 1.625em;
    padding: 1.625em !important;

    border: 1px solid #ddd;
    border-radius: 3px;
    background: #f6f6f6;
}

.commentlist .pingback {
    margin: 0 0 1.625em;
    padding: 0 1.625em;
}

.commentlist .children {
    margin: 0;

    list-style: none;
}

.commentlist .children li.comment {
    position: relative;

    margin: 1.625em 0 0;
    padding: 1.625em !important;

    border-left: 1px solid #ddd;
    border-radius: 0 3px 3px 0;
    background: #fff;
}

.commentlist .children li.comment .fn {
    display: block;
}

.comment-meta .fn {
    font-style: normal;
}

.comment-meta {
    color: #666;

    font-size: 12px;
    line-height: 2.2em;
}

.commentlist .children li.comment .comment-meta {
    margin-left: 50px;

    line-height: 1.625em;
}

.commentlist .children li.comment .comment-content {
    margin: 1.625em 0 0;
}

.comment-meta a {
    font-weight: bold;
}

.commentlist .avatar {
    position: absolute;
    top: 0;
    left: -102px;

    padding: 0;

    border-radius: 3px;
    -webkit-box-shadow: 0 1px 2px #ccc;
    box-shadow: 0 1px 2px #ccc;
}

.commentlist > li:before {
    position: absolute;
    left: -21px;

    content: url(img/comment-arrow.png);
}

.commentlist > li.pingback:before {
    content: '';
}

.commentlist .children .avatar {
    top: 2.2em;
    left: 2.2em;

    padding: 0;

    background: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

a.comment-reply-link {
    display: inline-block;

    padding: 0 8px;

    text-decoration: none;

    color: #666;
    border-radius: 3px;
    background: #eee;

    font-size: 12px;
}

a.comment-reply-link:hover, a.comment-reply-link:focus, a.comment-reply-link:active {
    color: #fff;
    background: #888;
}

a.comment-reply-link > span {
    position: relative;
    top: -1px;

    display: inline-block;
}

.nocomments {
    display: none;
}

/* Post author highlighting */
.commentlist > li.bypostauthor {
    border-color: #d3d3d3;
    background: #ddd;
}

.commentlist > li.bypostauthor .comment-meta {
    color: #575757;
}

.commentlist > li.bypostauthor:before {
    content: url(img/comment-arrow-bypostauthor.png);
}

/* Post Author threaded comments */
.commentlist .children > li.bypostauthor {
    border-color: #d3d3d3;
    background: #ddd;
}

/* Comment Form */
#respond {
    position: relative;

    overflow: hidden;

    padding: 1.625em;

    border: 0;
    background: #f5f5f5;
}

#respond input[type="text"], #respond textarea {
    position: relative;

    padding: 10px;

    text-indent: 80px;

    border: 5px solid #eee;
